import { customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { PublisherConfigBase, PublisherConfigElement } from '../models';

@customElement('usync-publisher-realtime-config')
export class uSyncRealtimePublisherConfigElement
	extends PublisherConfigBase
	implements PublisherConfigElement
{
	render() {
		return html`${this.renderSkip()} `;
	}

	renderSkip() {
		return html` <umb-property-layout
			label="Skip reporting"
			description="Skip the reporting steps (on click publish)">
			<div slot="editor">
				<uui-checkbox
					id="skipReport"
					label="Skip report"
					.checked=${this.options?.publisherSettings['skipReport'] as boolean}
					@change=${this.onPublisherSettingChange}>
					<div slot="label"></div
				></uui-checkbox>
			</div>
		</umb-property-layout>`;
	}
}

export default uSyncRealtimePublisherConfigElement;
